import { connect } from 'react-redux'
import { documentsBack, 
    documentsChangeViewMode, 
    documentsClick, 
    documentsDownload, 
    documentsNewFolder, 
    documentsRemove, 
    documentsSelect, 
    loadFolder, 
    documentsInitialize, 
    importerClick, 
    documentsChangeFilter } from '../actions'
import Documents from '../components/elements/Documents'

import confirm from '../scripts/confirm'

import { SORT_DATE, SORT_NAME } from '../constants'

import React from 'react'
import ConfirmModal from '../components/modals/ConfirmModal'

const modal = (
    <ConfirmModal>
        <p>
            Voulez-vous supprimer ce(s) lment(s) ?
        </p>
    </ConfirmModal>
)

const sortFiles = (files, filter, need) => {
    if(!need) return files

    console.log('Sort files',files.length);
    return files.sort((a, b) => {
        if(filter === SORT_DATE){
            a = new Date(a.updated_at)
            b = new Date(b.updated_at)
        } else if(filter === SORT_NAME) {
            a = a.name.toLowerCase()
            b = b.name.toLowerCase()
        }

        if(a < b) return -1
        if(b < a) return 1
        return 0
    })
}

const mapStateToProps = state => {
    return {
        files: sortFiles(state.documents.files, state.documents.filter, state.documents.needSort),
        status: state.documents.fetchStatus,
        folder: state.documents.folder,
        path: state.documents.path,
        fileCount: state.documents.fileCount,
        viewMode: state.viewMode,
        selectedCount: state.documents.selectedCount,
        filter: state.documents.filter,
        current: state.documents.current,
        lastType: state.documents.lastFetchType
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onFileClick: document => {
            dispatch(documentsClick(document))
        },
        onBackPress: () => {
            dispatch(documentsBack())
        },
        onChangeViewMode: () => {
            dispatch(documentsChangeViewMode())
        },
        onNewFolder: () => {
            const name = prompt('Nom du dossier')
            if(name)
                dispatch(documentsNewFolder(name))
        },
        onOpenFolder: path => {
            dispatch(loadFolder(path))
        },
        onRename: () => {
            console.log('RENAME No implemented yet')
        },
        onDownload: docs => {
            dispatch(documentsDownload(docs))
        },
        onRemove: docs => {
            confirm(modal)
            .then(() => dispatch(documentsRemove(docs)))
            .catch(err => {})
        },
        onDownloadAll: (docs) => {
            dispatch(documentsDownload(docs))
        },
        onSelect: document => {
            dispatch(documentsSelect(document))
        },
        onInitialize: () => {
            dispatch(documentsInitialize())
        },
        onImporterClick: path => {
            dispatch(importerClick(path))
        },
        onChangeFilter: filter => {
            dispatch(documentsChangeFilter(filter))
        }
    }
}

const ConnectedDocuments = connect(
    mapStateToProps,
    mapDispatchToProps
)(Documents)

export default ConnectedDocuments


