import React, {Component} from 'react'
import classNames from 'classnames'

import contactTypes from '../../constants/contactTypes.json'
import { createOptionsFromArray } from '../../tools'
import button from '../../styles/buttons.scss'
import style from '../../styles/ContactsModal.sass'

export default class ContactModal extends Component{
    constructor(props, context) {
        super(props, context)
        console.log('CM Constuctor', props);

        const item = props.item || {}

        this.state = {
            lastName: item.lastName || '',
            firstName: item.firstName || '',
            company: item.company || '',
            type: item.type || '',
            email: item.email || '',
            telephones: item.telephones || [],
            address: item.address || {
                line: '',
                code: '',
                city: ''
            },
            commentary: item.commentary || '',
            edit: false
        }

    }

    componentWillMount () {
        if(this.props.edit === true)
            this.state.edit = true
    }

    componentWillReceiveProps (newProps) {
        console.log('CM props', newProps)
    }

    save(e) {
        //create new object
        const { lastName, firstName, company, type, email, commentary } = this.state

        const telephones = [...this.state.telephones]
        const address = {...this.state.address}

        const newContact = {
            lastName,
            firstName,
            company,
            type,
            email,
            commentary,
            telephones,
            address
        }

        this.props.onSave(newContact)
        this.close(e)
    }

    close(e) {
        e.stopPropagation()
        
        //close the portal
        this.props.closePortal()
    }

    edit(e) {
        e.stopPropagation()

        this.setState({edit: true})
    }

    remove(e) {
        this.props.onRemove()
        this.close(e)
    }

    addTelephone(e) {
        e.preventDefault()
        e.stopPropagation()
        this.setState({
            telephones: [...this.state.telephones, {phone: '', type: 'fixe'}]
        })
    }

    renderEdition() {
        return (
            <div className={style.main} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                    <h1>{this.props.title}</h1>

                    <button className={classNames(button.button, button.default)} onClick={(e) => this.save(e)}>
                        <i className="fa fa-floppy-o"></i>Enregistrer
                    </button>

                    <button onClick={e => this.close(e)} className={classNames(button.button, button.stable, button.clear)}>
                        <i className="ion-ios-close-empty"></i>
                    </button>
                </div>
                <form className={style.content}>
                    <label>Nom</label>
                    <input
                        type="text"
                        value={this.state.lastName}
                        onChange={ e => this.setState({ lastName: e.target.value }) }
                        className="form-control"
                    />
                    <label>Prnom</label>
                    <input
                        type="text"
                        value={this.state.firstName}
                        onChange={ e => this.setState({ firstName: e.target.value }) }
                        className="form-control"
                    />
                    <label>Socit</label>
                    <input
                        type="text"
                        value={this.state.company}
                        onChange={ e => this.setState({ company: e.target.value }) }
                        className="form-control"
                    />

                    <label>Activit</label>
                    <select
                        type="text"
                        value={this.state.type}
                        onChange={ (e) => this.setState({type: e.target.value}) }
                        className="form-control"
                    >
                        <option>Aucune</option>
                        {createOptionsFromArray(contactTypes)}

                    </select>

                    <label>Tlphone</label>
                    <button className={classNames(button.button, button.stable)} onClick={(e) => this.addTelephone(e)}>
                        <i className="ion-ios-plus-empty"></i> Ajouter
                    </button>

                    {this.state.telephones && this.state.telephones.map((item, key) => (
                        <div key={key} className={style.telephone}>
                            <input
                                type="text"
                                value={item.phone}
                                onChange={e => {item.phone = e.target.value; this.setState({});}}
                                className="form-control"
                            />
                            <select
                                value={item.type}
                                onChange={e => {item.type = e.target.value; this.setState({});}}
                                className="form-control">
                                <option value="mobile">Mobile</option>
                                <option value="fixe">Fixe</option>
                            </select>
                        </div>
                    ))} 

                    <label>Email</label>
                    <input
                        type="email"
                        value={this.state.email}
                        onChange={ e => this.setState({ email: e.target.value }) }
                        className="form-control"
                    />

                    <label>Adresse</label>
                    <input
                        type="text"
                        placeholder="Adresse"
                        value={this.state.address.line}
                        onChange={ e => this.setState({ address: {...this.state.address, line: e.target.value} }) }
                        className="form-control"
                    />
                    <input
                        type="text"
                        placeholder="Code postal"
                        value={this.state.address.code}
                        onChange={ e => this.setState({ address: {...this.state.address, code: e.target.value} }) }
                        className="form-control"
                    />
                    <input
                        type="text"
                        placeholder="Ville"
                        value={this.state.address.city}
                        onChange={ e => this.setState({ address: {...this.state.address, city: e.target.value} }) }
                        className="form-control"
                    />

                    <label>Commentaire</label>
                    <textarea
                        value={this.state.commentary}
                        onChange={ e => this.setState({ commentary: e.target.value }) }
                        className="form-control"
                    />
                </form>
            </div>
        )
    }

    renderView() {
        return (
            <div className={style.main} onClick={e => e.stopPropagation()}>
                <div className={style.header}>
                    <h1>{this.state.firstName} {this.state.lastName && this.state.lastName.toUpperCase()}</h1>

                    <button className={classNames(button.button, button.default)} onClick={(e) => this.edit(e)}>
                        <i className="fa fa-edit"></i>Modifier
                    </button>

                     <button onClick={e => this.remove(e)} className={classNames(button.button, button.assertive)}>
                        <i className="ion-ios-trash-outline"></i>Supprimer
                    </button>

                    <button onClick={e => this.close(e)} className={classNames(button.button, button.stable, button.clear)}>
                        <i className="ion-ios-close-empty"></i>
                    </button>
                </div>
                <div className={style.content}>
                    <div>
                        <h3>Socit</h3>
                        <p>{this.state.company}</p>
                    </div>

                    <div>
                        <h3>Type / Spcialit</h3>
                        <p>{this.state.type}</p>
                    </div>

                    <div>
                        <h3>Tlphone</h3>
                        {this.state.telephones && this.state.telephones.map((item, index) => (
                            <p key={index}><strong>{item.type && item.type.toUpperCase()+' :'}</strong><a href={"callto:"+item.phone}>{item.phone}</a></p>
                        ))}
                    </div>

                    <div>
                        <h3>Email</h3>
                        <p><a href={"mailto:"+this.state.email}>{this.state.email}</a></p>
                    </div>

                    <div>
                        <h3>Adresse</h3>
                        <p>{this.state.address.line}</p>
                        <p>{this.state.address.code}</p>
                        <p>{this.state.address.city}</p>
                    </div>

                    <div>
                        <h3>Commentaire</h3>
                        <div>
                            {this.state.commentary && this.state.commentary.split('\n').map((item, key) => (
                                <div key={key}>{item}</div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    render() {
        if(this.state.edit)
            return this.renderEdition()
        else
            return this.renderView()
    }

}


