import {
    combineReducers
} from 'redux'

import {
    LOAD_DATA,
    LOAD_DATA_SUCCESS,
    LOAD_DATA_FAILURE,
    // LOAD_DIAGNOSIS_SUCCESS,
    // LOAD_EQUIPMENTS_SUCCESS,
    // LOAD_HOME_SUCCESS,
    // LOAD_MAINTENANCES_SUCCESS,
    // LOAD_WORKS_SUCCESS,
    DATA_NEW_SUCCESS,
    DATA_REMOVE_SUCCESS,
    DATA_SAVE_SUCCESS
} from '../actions'

import {
    DOCTYPE_DIAGNOSIS,
    DOCTYPE_EQUIPMENTS,
    DOCTYPE_HOMEDATA,
    DOCTYPE_MAINTENANCES,
    DOCTYPE_WORKS,
    DOCTYPE_DATA
} from '../constants'

export const data = (state = [], action) => {
    switch (action.type) {
        case LOAD_DATA_SUCCESS:
        // case LOAD_WORKS_SUCCESS:
        // case LOAD_HOME_SUCCESS:
        // case LOAD_EQUIPMENTS_SUCCESS:
        // case LOAD_MAINTENANCES_SUCCESS:
        // case LOAD_DIAGNOSIS_SUCCESS:
            return action.data
        case DATA_NEW_SUCCESS:
            return [...state, action.data]
        case DATA_REMOVE_SUCCESS:
            return state.filter(f => f._id === action.id)
        case DATA_SAVE_SUCCESS:
            const withoutData = state.filter(f => f._id !== action.data._id)
            return [...withoutData, action.data]
        default:
            return state
    }
}

export const doctype = (state = null, action) => {
    switch (action.type) {
        case LOAD_DATA_SUCCESS:
            return action.doctype
        // case LOAD_DIAGNOSIS_SUCCESS:
        //     return DOCTYPE_DIAGNOSIS
        // case LOAD_WORKS_SUCCESS:
        //     return DOCTYPE_WORKS
        // case LOAD_MAINTENANCES_SUCCESS:
        //     return DOCTYPE_MAINTENANCES
        // case LOAD_HOME_SUCCESS:
        //     return DOCTYPE_HOMEDATA
        // case LOAD_EQUIPMENTS_SUCCESS:
        //     return DOCTYPE_EQUIPMENTS
        default:
            return state
    }
}

export default combineReducers({
    data,
    doctype
})


